.itemWrap {
  display: inline;
}
.item {
  position: relative;
  display: inline-block;
  width: 25%;
  padding: 0 5px;
  box-sizing: border-box;
}
.posterWrap {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 145%;
}
.posterWrap:hover .reviewimgWrap {
  display: block;
  box-shadow: 1px 1px 6px #b3b3b3;
}
.poster {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.reviewimgWrap {
  display: none;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.photoCard {
  width: 100%;
  height: 100%;
  background-color: white;
  position: relative;
  overflow-y: scroll;
  padding: 10%;
  padding-bottom: 24%;
  box-sizing: border-box;
  border-radius: 10px;
}
.reviewImg {
  width: 100%;
}
.title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
  margin-top: 5px;
  color: rgb(97, 93, 93);
  font-size: 16px;
}
.myRate {
  margin: 0;
  margin-bottom: 5px;
  color: black;
  font-size: 12px;
}
.button {
  width: 100%;
  border-style: none;
  font-size: 18px;
  font-weight: bold;
  padding: 10px 40px;
  border-radius: 5px;
  cursor: pointer;
  background-color: rgb(30, 82, 131);
  color: white;
}
.button:hover {
  background-color: rgb(183, 202, 219);
  color: black;
}
